.ship-for-animation {
    position: absolute;
    height: 38vh;
    -webkit-animation: ship-entry-x 3s infinite, ship-entry-rotation 3s infinite;
            animation: ship-entry-x 3s infinite, ship-entry-rotation 3s infinite;
    
}

.ship-for-animation::-webkit-scrollbar {
    display: none;
}


.div-for-animation {
    position: relative;
    z-index: 1;
    top: 50vh;
    left: -40vw;
    -webkit-animation: ship-entry-y 3s infinite, ship-entry-opacity 3s infinite;
            animation: ship-entry-y 3s infinite, ship-entry-opacity 3s infinite;
}

.div-for-animation::-webkit-scrollbar {
    display: none;
}

@-webkit-keyframes ship-entry-y {
    0% {
        top: 50vh;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        top: 30vh;
        -webkit-transform: scale(0.15);
                transform: scale(0.15);
    }
}

@keyframes ship-entry-y {
    0% {
        top: 50vh;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        top: 30vh;
        -webkit-transform: scale(0.15);
                transform: scale(0.15);
    }
}

@-webkit-keyframes ship-entry-x {
    0% {
        left: -90vw;
    }

    100% {
        left: 430vw;
    }
}

@keyframes ship-entry-x {
    0% {
        left: -90vw;
    }

    100% {
        left: 430vw;
    }
}

@-webkit-keyframes ship-entry-rotation {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    30% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
    100% {-webkit-transform: rotate(-120deg);transform: rotate(-120deg);}
}

@keyframes ship-entry-rotation {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    30% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
    100% {-webkit-transform: rotate(-120deg);transform: rotate(-120deg);}
}


@-webkit-keyframes ship-entry-opacity {
    0% {z-index: 1;}
    60% {z-index: 1;}
    100% {z-index: -888;}
}


@keyframes ship-entry-opacity {
    0% {z-index: 1;}
    60% {z-index: 1;}
    100% {z-index: -888;}
}





