.theNight-btn {
    width: 60px;
    height: 60px;
    background: rgb(107,0,255);
    background: linear-gradient(30deg, rgba(107,0,255,1) 0%, rgba(147,40,255,1) 50%, rgba(0,165,255,1) 100%);
    border-radius: 15px;
    border: 0px;
    margin-bottom: 20vh;
    outline: rgb(17, 17, 17) solid 26px;
    outline-offset: -30px;
    box-shadow: rgba(54, 0, 247, 0.521) 0px 0px 60px 0px;
    transition: 0.8s;

}

.theNight-btn:active{
    outline: rgba(164, 167, 175, 0.158) solid 26px;
    outline-offset: -30px;
    border-radius: 15px;
    transition: 0.5s;
}

.backGroundBlack{
    background: rgba(164, 167, 175, 0.158)
}

.backGroundWhite{
    background: rgb(252, 252, 252);
}

.secretPassword{
    width: 200px;
    height: auto;
}